function login (that, username, password) {
  return that.$axios({
    method: 'post',
    url: '/sketch/api/v1/user/login',
    params: {
      username: username,
      password: password
    }
  })
}

function registry (that, username, password, phone, nickname) {
  return that.$axios({
    method: 'post',
    url: '/sketch/api/v1/user/addUser',
    params: {
      username: username,
      password: password,
      nickname: nickname,
      phone: phone
    }
  })
}

export {
  login,
  registry
}
