import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../views/Login.vue'
import Draw from '../views/Draw.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login
  },
  {
    path: '/drawing',
    name: 'draw',
    component: Draw
  }
]

const router = new VueRouter({
  routes
})

export default router
