<template>
  <div class="background">
    <transition name="el-fade-in-linear">
      <el-tabs v-show="loginShow" v-model="loginTabs" type="card" style="padding-right: 50px;" class="loginMain">
        <el-tab-pane label="登陆" name="loginTab" @keyup.enter.native="submitLoginForm">
          <h2>MySketch手绘图形打分系统</h2>
          <el-form :model="loginForm" :rules="loginRule" ref="loginForm" label-width="100px">
            <el-form-item label="用户名" prop="username">
              <el-input v-model="loginForm.username"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input v-model="loginForm.password" type="password" :clearable="true"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitLoginForm">登陆</el-button>
            </el-form-item>
            <p>您浏览器的 cookies 设置必须打开
              <el-tooltip effect="dark" content="如果您禁用cookies,会导致登陆验证失效" placement="right">
                <i class="el-icon-question"></i>
              </el-tooltip>
            </p>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="注册" name="registryTab" @keyup.enter.native="submitRegistryForm">
          <h2>MySketch手绘图形打分系统</h2>
          <el-form :model="registryForm" :rules="registryRule" ref="registryForm" label-width="100px">
            <el-form-item label="手机号" prop="phone">
              <el-input v-model="registryForm.phone"></el-input>
            </el-form-item>
            <el-form-item label="用户名" prop="username">
              <el-input v-model="registryForm.username"></el-input>
            </el-form-item>
            <el-form-item label="昵称" prop="nickname">
              <el-input v-model="registryForm.nickname"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input v-model="registryForm.password" type="password" :clearable="true"></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="repeatPassword">
              <el-input v-model="registryForm.repeatPassword" type="password" :clearable="true"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitRegistryForm">注册</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
    </transition>
  </div>
</template>

<script>
import { login, registry } from '../api/user'
import { createCookie } from '../lib/cookie'

export default {
  name: 'Home',
  mounted: function () {
    this.loginShow = true
  },
  methods: {
    submitLoginForm () {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          const result = login(this, this.loginForm.username, this.loginForm.password)
          result.then(function (res) {
            if (res.data === true) {
              createCookie('login', this.loginForm.username, 3)
              this.$router.push('/draw')
            } else {
              this.showMsg(this, 'error', '您的用户名或密码不正确')
            }
            console.log(res)
          }.bind(this)).catch(function (err) {
            this.showMsg(this, 'error', '网络连接异常')
            console.log(err)
          }.bind(this))
        } else {
          this.showMsg(this, 'error', '请填写完整的信息')
        }
      })
    },
    submitRegistryForm () {
      this.$refs.registryForm.validate((valid) => {
        if (valid) {
          const result = registry(this, this.registryForm.username, this.registryForm.password, this.registryForm.phone, this.registryForm.nickname)
          result.then(function (res) {
            console.log(res)
            this.showMsg(this, 'success', '注册成功')
          }.bind(this)).catch(function (err) {
            console.log(err)
            this.showMsg(this, 'error', '注册失败，请检查您的网络连接')
          }.bind(this))
        } else {
          this.showMsg(this, 'error', '请填写完整的信息')
        }
      })
    }
  },
  data () {
    const validatePassword = (rule, value, cb) => {
      const blank = /^\S*$/
      const pattern = /^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)[0-9A-Za-z]{8,20}$/g
      if (!pattern.test(value) || !blank.test(value)) {
        cb(new Error('请输入8-20个非空白字符，且至少包括数字和大小写字符中的两种'))
      } else {
        if (this.registryForm.repeatPassword !== '') {
          this.$refs.registryForm.validateField('repeatPassword')
        }
        cb()
      }
    }
    const validateRepeatPassword = (rule, value, cb) => {
      if (value !== this.registryForm.password) {
        cb(new Error('两次输入密码不一致!'))
      } else {
        cb()
      }
    }
    const validatePhone = (rule, value, cb) => {
      cb()
    }
    return {
      dialogVisible: false,
      loginShow: false,
      loginTabs: 'loginTab',
      loginForm: {
        username: '',
        password: ''
      },
      loginRule: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      },
      registryForm: {
        phone: '',
        username: '',
        nickname: '',
        password: '',
        repeatPassword: ''
      },
      registryRule: {
        phone: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { min: 11, max: 11, message: '请输入格式正确的手机号码', trigger: 'blur' },
          { validator: validatePhone, trigger: 'blur' }
        ],
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        nickname: [
          { required: true, message: '请输入昵称', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { validator: validatePassword, trigger: 'blur' }
        ],
        repeatPassword: [
          { required: true, message: '请再次输入密码', trigger: 'blur' },
          { validator: validateRepeatPassword, trigger: 'blur' }
        ]
      }
    }
  }
}
</script>

<style scoped>
  .background {
    background-size: 100% 100%;
    background: url("../assets/background.jpg") center center no-repeat;
    position: fixed;
    height: 100%;
    width: 100%;
    overflow-y: hidden;
    overflow-x: hidden;
  }
  .loginMain {
    background: whitesmoke;
    text-align: center;
    width: 35vw;
    height: 45vh;
    position: absolute;
    left: 50%;
    top: 30%;
    transform: translate(-50%, -10%);
  }
</style>
